import React from "react"
import Bio from "./pages/Bio"
import Page from "./pages/Page"
import { HashRouter as Router, Redirect, Route, Switch } from "react-router-dom"

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/">
          <Redirect to="/Home" />
        </Route>
        <Route exact path="/Bio">
          <Bio />
        </Route>
        <Route exact path="/:slug">
          <Page />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
